@import url(https://fonts.googleapis.com/css2?family=Comic+Neue&family=Quicksand&display=swap);
.home-page{position:relative}.home-page h1{position:fixed;top:0;left:1em;font-family:'Comic Neue', cursive;font-size:2.5rem;font-weight:100;color:#222;width:0;white-space:nowrap;overflow:hidden;-webkit-animation:showHeader 4s ease forwards 1;animation:showHeader 4s ease forwards 1;-webkit-animation-delay:7s;animation-delay:7s}@-webkit-keyframes showHeader{to{width:100%}}@keyframes showHeader{to{width:100%}}@media screen and (max-width: 600px){.home-page h1{transform:rotate(270deg);transform-origin:left;top:70vh;left:24px}@-webkit-keyframes showHeader{to{width:100vh}}@keyframes showHeader{to{width:100vh}}}@media screen and (max-width: 600px) and (max-height: 600px){.home-page h1{font-size:2rem}}@media screen and (max-width: 600px) and (max-height: 460px){.home-page h1{font-size:1.5rem}}@media screen and (max-width: 600px) and (max-height: 340px){.home-page h1{font-size:1.2rem;top:90vh}}body.home{overflow:hidden}

.svg-scene{height:100vh;max-height:1280px}.svg-scene svg{width:100%;height:100%;overflow:hidden;-webkit-animation:sky 2s ease forwards;animation:sky 2s ease forwards;-webkit-animation-delay:4s;animation-delay:4s;background-image:radial-gradient(at top right, #458f94, #e2fdff, #e2fdff);background-position:bottom left;background-size:300%}@media screen and (max-width: 700px){.svg-scene svg{-webkit-animation-delay:3s;animation-delay:3s}}@-webkit-keyframes sky{to{background-position:center center;background-size:100%}}@keyframes sky{to{background-position:center center;background-size:100%}}.svg-scene svg .grass{transform:translateY(calc(100% - 120px))}.svg-scene svg .grass .blade{-webkit-animation:grow, wobble infinite alternate;animation:grow, wobble infinite alternate;transform-origin:50% 100%}@-webkit-keyframes grow{from{transform:scaleY(0)}to{transform:scaleY(1)}}@keyframes grow{from{transform:scaleY(0)}to{transform:scaleY(1)}}@-webkit-keyframes wobble{from{transform:translateY(0)}to{transform:translateY(20px)}}@keyframes wobble{from{transform:translateY(0)}to{transform:translateY(20px)}}.svg-scene svg .sun{fill:yellow;transform-origin:150px 150px;-webkit-animation:moveIn, shine;animation:moveIn, shine;-webkit-animation-fill-mode:forwards, forwards;animation-fill-mode:forwards, forwards;-webkit-animation-duration:5s, 4s;animation-duration:5s, 4s;-webkit-animation-delay:0s, 7s;animation-delay:0s, 7s;-webkit-animation-direction:normal, alternate;animation-direction:normal, alternate;-webkit-animation-iteration-count:1, infinite;animation-iteration-count:1, infinite;-webkit-animation-timing-function:ease-in-out, ease;animation-timing-function:ease-in-out, ease}@-webkit-keyframes moveIn{0%{opacity:0;transform:translate(0, 0) scale(0.5)}25%{opacity:.8}100%{opacity:1;transform:translate(100vw, -180px) scale(1)}}@keyframes moveIn{0%{opacity:0;transform:translate(0, 0) scale(0.5)}25%{opacity:.8}100%{opacity:1;transform:translate(100vw, -180px) scale(1)}}@-webkit-keyframes shine{0%{fill:yellow}100%{fill:gold}}@keyframes shine{0%{fill:yellow}100%{fill:gold}}@media screen and (max-width: 700px){.svg-scene svg .sun{-webkit-animation-duration:4s, 4s;animation-duration:4s, 4s;-webkit-animation-delay:0s, 6s;animation-delay:0s, 6s}@-webkit-keyframes moveIn{0%{opacity:0;transform:translate(0, 0) scale(0.4)}25%{opacity:.8}100%{opacity:1;transform:translate(calc(100vw - 40px), -180px) scale(0.7)}}@keyframes moveIn{0%{opacity:0;transform:translate(0, 0) scale(0.4)}25%{opacity:.8}100%{opacity:1;transform:translate(calc(100vw - 40px), -180px) scale(0.7)}}}.svg-scene svg .sun .sun-shine{stroke:yellow;stroke-width:2px}.svg-scene svg .sun .sun-shine:nth-of-type(even){stroke:gold}.svg-scene svg .cloud{cursor:pointer;transform:translate(100vw, 0);-webkit-animation-name:moveInFL, cloudColorChange;animation-name:moveInFL, cloudColorChange;-webkit-animation-duration:3s;animation-duration:3s;-webkit-animation-delay:2s;animation-delay:2s;-webkit-animation-iteration-count:1;animation-iteration-count:1;-webkit-animation-timing-function:ease;animation-timing-function:ease;-webkit-animation-direction:normal;animation-direction:normal;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards}.svg-scene svg .cloud:nth-of-type(odd){animation-direction:normal, reverse;-webkit-animation-name:moveInFR;animation-name:moveInFR;transform:translate(-100vw, 0)}@-webkit-keyframes moveInFR{from{transform:translate(100vw, 0)}to{transform:translate(0, 0)}}@keyframes moveInFR{from{transform:translate(100vw, 0)}to{transform:translate(0, 0)}}@-webkit-keyframes moveInFL{from{transform:translate(-100vw, 0)}to{transform:translate(0, 0)}}@keyframes moveInFL{from{transform:translate(-100vw, 0)}to{transform:translate(0, 0)}}.svg-scene svg .cloud text{font-size:1.4rem;fill:#222}.svg-scene svg .cloud .base,.svg-scene svg .cloud .big,.svg-scene svg .cloud .small{fill:#fff;-webkit-animation-duration:2s, 12s;animation-duration:2s, 12s;-webkit-animation-delay:2s, 4s;animation-delay:2s, 4s;-webkit-animation-iteration-count:infinite, infinite;animation-iteration-count:infinite, infinite;-webkit-animation-timing-function:ease, ease;animation-timing-function:ease, ease;-webkit-animation-direction:alternate, alternate;animation-direction:alternate, alternate;-webkit-animation-fill-mode:forwards, forwards;animation-fill-mode:forwards, forwards}@-webkit-keyframes cloudColorChange{0%{fill:#fee}50%{fill:#fff}100%{fill:#ffd}}@keyframes cloudColorChange{0%{fill:#fee}50%{fill:#fff}100%{fill:#ffd}}.svg-scene svg .cloud .base{-webkit-animation-name:wobbleBase, cloudColorChange;animation-name:wobbleBase, cloudColorChange;-webkit-animation-duration:2s, 12s;animation-duration:2s, 12s}@-webkit-keyframes wobbleBase{from{transform:translate(-5px, 0px)}to{transform:translate(5px, 0px)}}@keyframes wobbleBase{from{transform:translate(-5px, 0px)}to{transform:translate(5px, 0px)}}.svg-scene svg .cloud .big{-webkit-animation-name:wobbleBig, cloudColorChange;animation-name:wobbleBig, cloudColorChange;-webkit-animation-duration:2s, 12s;animation-duration:2s, 12s}@-webkit-keyframes wobbleBig{from{transform:translate(-5px, 5px)}to{transform:translate(0px, 0px)}}@keyframes wobbleBig{from{transform:translate(-5px, 5px)}to{transform:translate(0px, 0px)}}.svg-scene svg .cloud .small{-webkit-animation-name:wobbleSmall, cloudColorChange;animation-name:wobbleSmall, cloudColorChange;-webkit-animation-duration:2s, 12s;animation-duration:2s, 12s}@-webkit-keyframes wobbleSmall{from{transform:translate(10px, 10px)}to{transform:translate(0px, 0px)}}@keyframes wobbleSmall{from{transform:translate(10px, 10px)}to{transform:translate(0px, 0px)}}.svg-scene .ground{height:100%;background:green;margin-top:-4px}

.loading{transition:opacity 2s ease;position:absolute;display:flex;justify-content:center;align-items:center;width:100%;height:100%;z-index:99998}.loading:not(.loading-fs)[data-active=yes]{display:flex}.loading:not(.loading-fs)[data-active=no]{display:none}.loading-fs{background-color:var(--body-bg-color);width:100vw;height:100vh;position:fixed;top:0;left:0;bottom:0;right:0;z-index:99999}.loading-fs[data-active=yes]{opacity:1}.loading-fs[data-active=no]{left:100vw;opacity:0;transition:opacity 2s ease, left 0s linear 2s}

.markdown-to-jsx{color:#333;width:100%;padding:1em;max-width:820px;margin:0 auto;word-wrap:break-word;line-height:1.4em;font-size:1.1rem}.markdown-to-jsx h1,.markdown-to-jsx h2,.markdown-to-jsx h3,.markdown-to-jsx h4,.markdown-to-jsx h5,.markdown-to-jsx h6{font-weight:100;color:#222;line-height:1em;margin-top:1em;margin-bottom:.4em}.markdown-to-jsx h1.hidden,.markdown-to-jsx h2.hidden,.markdown-to-jsx h3.hidden,.markdown-to-jsx h4.hidden,.markdown-to-jsx h5.hidden,.markdown-to-jsx h6.hidden{opacity:0;color:transparent;height:0}.markdown-to-jsx h1{font-size:2.5rem}.markdown-to-jsx h2{font-size:2.2rem}.markdown-to-jsx h3{font-size:2rem}.markdown-to-jsx h4{font-size:1.5rem}.markdown-to-jsx h5{font-size:1.3rem}.markdown-to-jsx h6{font-size:1.2rem}.markdown-to-jsx ul,.markdown-to-jsx ol{margin-top:1rem;margin-bottom:1rem;text-align:left}.markdown-to-jsx ul ul,.markdown-to-jsx ul ol,.markdown-to-jsx ol ul,.markdown-to-jsx ol ol{margin-top:0;margin-bottom:0}.markdown-to-jsx ol{margin-left:16px}.markdown-to-jsx a:-webkit-any-link{color:currentColor;text-decoration:underline}.markdown-to-jsx a,.markdown-to-jsx a:link,.markdown-to-jsx a:visited,.markdown-to-jsx a:active,.markdown-to-jsx a:any-link,.markdown-to-jsx a:focus{color:currentColor;text-decoration:underline}.markdown-to-jsx code{background-color:#ddd;color:#444;padding:0 4px;font-family:monospace}.markdown-to-jsx pre{padding:8px;margin:1rem 0;text-align:left;overflow-x:auto;background-color:#ddd}.markdown-to-jsx pre code{padding:0;background-color:transparent;font-size:1em}.markdown-to-jsx pre code .hljs-comment{color:#777}.markdown-to-jsx blockquote{background-color:rgba(200,200,100,0.3);border-left:4px solid #4e4e1e;padding:.5rem 1rem .5rem 2rem;margin:1rem 0;font-style:italic}@media screen and (max-width: 700px){.markdown-to-jsx blockquote{padding-left:1rem;border-left:none}}.markdown-to-jsx img{max-width:100%}@media screen and (max-width: 700px){.markdown-to-jsx img{max-width:100vw;margin-left:-1rem}}.markdown-to-jsx hr{margin:2rem 0;background-color:#666;border:none;height:1px}.markdown-to-jsx table{border-collapse:collapse;margin:0 auto}.markdown-to-jsx table td,.markdown-to-jsx table th{border:1px solid #222;padding:.25rem .5rem}

.edit-markdown-page{flex-grow:1;display:flex}.edit-markdown-page .left{flex-grow:1;min-width:400px;max-width:900px;display:flex;flex-direction:column}.edit-markdown-page .left textarea{flex-grow:1;padding:1em .5em}.edit-markdown-page .markdown-to-jsx{flex-grow:1;width:50%;max-width:none;min-width:400px;padding:1em}@media screen and (max-width: 800px){.edit-markdown-page .markdown-to-jsx{display:none}}.edit-markdown-page .save-btn{position:fixed;top:6em;right:1em;padding:1em;transition:opacity .4s ease}.edit-markdown-page .save-btn:disabled{opacity:.4}

.page-content{position:absolute;width:100%;height:100%;display:flex;flex-direction:column;flex-grow:1}.page-content>*:not(.footer){flex-grow:1}

.footer{transition:background-color 1s ease;z-index:99;transform:translateY(100%);box-shadow:0 0 5px rgba(40,40,40,0.4);padding:1em}

.page{flex-grow:1;display:flex;flex-direction:column}@media screen and (min-width: 720px){.page{flex-direction:column}}.page .content{flex-grow:1;display:flex;flex-direction:column}.page .content main{position:relative}

.nav{position:relative;z-index:98;transition:background-color 1s ease;padding:1em}

main{display:flex;flex-direction:column;flex-grow:1}a:-webkit-any-link{color:currentColor}a,a:valid,a:any-link,a:visited,a:active{color:currentColor}

body{font-family:'Quicksand', sans-serif;margin:0;padding:0;overflow-x:hidden;display:flex;flex-direction:column;min-height:100vh}#root{display:flex;flex-direction:column;flex-grow:1}*,*::before,*::after{box-sizing:border-box}

